import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, FormLayout, FormLayoutGroup, Select, Radio, Checkbox, Button, Input, Textarea, Link} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

const TestPanel = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
		</PanelHeader>
    <FormLayout>
      <Input type="email" top="E-mail" />
      <FormLayoutGroup top="" bottom="       .">
        <Input type="password"  placeholder=" " />
        <Input type="password" placeholder=" " />
      </FormLayoutGroup>
      <Input top="" />
      <Input top="" />
      <Select top="" placeholder=" ">
        <option value="m"></option>
        <option value="f"></option>
      </Select>
      <div top=" ">
        <Radio name="type"></Radio>
        <Radio name="type"></Radio>
      </div>
      <Textarea top=" " />
      <FormLayoutGroup>
        <Checkbox>   <Link></Link></Checkbox>
        <div>
          <Button type="cell"></Button>
        </div>
      </FormLayoutGroup>
    </FormLayout>
	</Panel>
);

TestPanel.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default TestPanel;



